@import url(https://fonts.googleapis.com/css?family=Rubik:400,500,600,700&display=swap);


* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: 'Rubik', sans-serif;
  overflow-x: hidden;
}

input,
textarea,
button {
  font-family: inherit;
}

body {
  display: block;
  background: #fafafa;
}

@media (max-width: 48em) {
  html {
    font-size: 56.25%;
  }
}

@media (max-width: 24em) {
  html {
    font-size: 50%;
  }
}

.center-text {
  text-align: center;
}

.center-flex-row {
  display: flex;
  justify-content: center;
  align-items: center;
}

.center-flex-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.no-data-found {
  padding: 12.8rem;
  font-weight: 700;
  font-size: 2.4rem;
  color: #333;
}

.tiny-gap {
  grid-gap: 0.8rem;
  gap: 0.8rem;
}
.extra-small-gap {
  grid-gap: 1.6rem;
  gap: 1.6rem;
}
.small-gap {
  grid-gap: 2.4rem;
  gap: 2.4rem;
}
.medium-gap {
  grid-gap: 4.8rem;
  gap: 4.8rem;
}
.large-gap {
  grid-gap: 9.6rem;
  gap: 9.6rem;
}


.slide-in-right-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.slide-in-right-enter-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

.slide-in-right-exit {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
}

.slide-in-right-exit-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.main-header {
  padding: 0 11.2rem;
  width: 100%;
  height: 10vh;

  justify-content: space-between;
  display: flex;
  align-items: center;
}

@media (max-width: 768px) {
  .main-header {
    position: fixed;
    top: 0;
    z-index: 1;
    background-color: #fafafa;
    padding: 0 4.8rem;
  }
  main {
    margin-top: 9.6rem;
  }
}

.side-drawer {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 99;
  height: 100vh;
  width: 68%;
  box-shadow: 0 7px 30px -10px rgba(150, 170, 180, 0.4);
  background: rgb(255, 255, 255, 0.9);
  /* backdrop-filter: blur(10px); */
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgb(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  z-index: 9;
}

.button {
  display: inline-block;
  font: inherit;
  font-weight: 600;
  font-size: 1.4rem;
  padding: 0.6rem 1.2rem;
  /* color: #767676; */
  /* background: transparent; */
  cursor: pointer;
  text-decoration: none;
  /* border: 1px solid #b2bec3; */
  border-radius: 0.6rem;
  transition: all 0.3s;
}

.button--primary {
  background-color: #333;
  color: #fff;
  border: none;
}

.button--secondary {
  background: transparent;
  color: #767676;
  border: 1px solid #b2bec3;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active,
.button:focus {
  color: #333;
  border-color: #333;
}

.button--white-bg {
  background: #fff;
}

.button--inverse:hover,
.button--inverse:active,
.button--inverse:focus {
  color: #fff;
  background: #636e72;
  border-color: #636e72;
}

.button--danger:hover,
.button--danger:active,
.button--danger:focus {
  color: #fff;
  background: #ff7675;
  border-color: #ff7675;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

/* default button font-size: 1.4rem */

.button--small {
  font-size: 0.8rem;
}
.button--medium {
  font-size: 1.6rem;
}
.button--large {
  font-size: 2.4rem;
}

.nav-links {
  list-style: none;
  width: 100%;
  height: 100%;
}

.nav-links a:link,
.nav-links a:visited {
  text-decoration: none;
  font-size: 1.6rem;
  font-weight: 500;
  color: #767676;
}

.nav-links a:hover,
.nav-links a:active {
  color: #333;
  transition: all 0.3s;
}

@media (max-width: 767px) {
  .nav-links {
    flex-direction: column;
  }

  .nav-links a:link,
  .nav-links a:visited,
  .nav-links a:hover,
  .nav-links a:active {
    color: #333;
    font-size: 2.4rem;
  }
}

.main-navigation__menu-btn {
  width: 2.4rem;
  height: 2.4rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 2.4rem;
  height: 2px;
  background: #333;
}

.main-navigation__title a:link,
.main-navigation__title a:visited,
.main-navigation__title a:hover,
.main-navigation__title a:active {
  font-size: 2.4rem;
  font-weight: 700;
  text-decoration: none;
  color: #333;
}

.main-navigation__title svg {
  font-size: 2.4rem;
  color: #333;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-navigation__links {
    display: none;
  }
}

.footer-section {
  padding: 8rem;
  font-size: 1.2rem;
  color: #767676;
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #555;
  border-color: #555 transparent #555 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.section-hero {
  height: 45vh;

  position: relative;

  background-image: linear-gradient(0deg, #3333334d, #3333334d),
    url(/static/media/hero-bg.2af6d894.webp);
  background-size: cover;
  background-position: center;
  background-blend-mode: normal;
}

.hero-heading {
  font-size: 4.4rem;
  color: #fff;
  text-shadow: 0px 7px 30px #333333cc;
}

.hero-description {
  font-size: 1.8rem;
  color: #fff;
  text-shadow: 0px 7px 30px #333333cc;
}

.hero-cta {
  position: absolute;
  top: 100%;
  -webkit-transform: translate(0%, -50%);
          transform: translate(0%, -50%);
}

.hero-cta--actions {
  position: relative;
}

.hero-cta--actions button {
  box-shadow: 0px 8px 20px rgb(0, 0, 0, 6%);
}

.hero-cta--text {
  font-size: 1.2rem;
  color: #555;

  position: absolute;
  top: 100%;
  -webkit-transform: translate(0, 0.8rem);
          transform: translate(0, 0.8rem);
}

.hero-cta--text span {
  font-weight: 500;
  color: #333;
}

.section-features {
  height: 45vh;
  padding: 12.8rem;

  position: relative;
}


.feature-icon {
  font-size: 4.4rem;
  color: #333;
}

.feature-title {
  font-size: 1.8rem;
  color: #333;
  font-weight: 600;
}

.feature-text {
  font-size: 1.4rem;
  color: #555;
}

.scroll-down {
  position: absolute;
  top: 100%;
  left: 50%;
  /* transform: translate(0, -4rem); */
  -webkit-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
}

.scroll-down svg {
  font-size: 2.4rem;
  color: #767676;
}

@media (max-width: 64em) {
  .section-features {
    padding: 11.2rem;

    grid-gap: 4.8rem;

    gap: 4.8rem;
  }
}

@media (max-width: 48em) {
  .section-features {
    padding: 8rem 6.4rem;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 8rem;
    -webkit-column-gap: 8rem;
            column-gap: 8rem;
    grid-row-gap: 6.4rem;
    row-gap: 6.4rem;
  }

  .scroll-down {
    display: none;
  }
}

.avatar {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.avatar img {
  display: block;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card {
  position: relative;
  margin: 0;
  padding: 4.8rem;
  box-shadow: 0 7px 30px -10px rgba(150, 170, 180, 0.4);
  border-radius: 11px;
  overflow: hidden;
  background: #fff;
  transition: all 0.25s;
}

.user-item {
  min-width: 20rem;
  transition: all 0.3s;
}

.user-item:hover {
  box-shadow: 0 7px 30px -10px rgba(150, 170, 180, 0.9);
  -webkit-transform: translateY(-1.2rem);
          transform: translateY(-1.2rem);
}

.user-item li {
  width: 100%;

  text-decoration: none;
  background: #fff;
}

.user-item__image {
  width: 6.4rem;
  height: 6.4rem;
}

.user-item__info {
  height: 11.2rem;
  width: 100%;

  text-align: center;
}

.user-item__info-name {
  font-size: 2.4rem;
  font-weight: 600;
  color: #333;

  margin-bottom: 1.6rem;
}

.user-item__info-text {
  font-size: 1.4rem;
  font-weight: 400;
  color: #767676;

  line-height: 1.6;
}

.users-section {
  padding: 6.4rem 8rem;
}

.users-section h2 {
  font-size: 1.4rem;
  color: #767676;
  font-weight: 500;

  margin-bottom: 6.4rem;
}

.users-list {
  list-style: none;
  margin: 0 auto;
  width: 80%;
  max-width: 100rem;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 6.4rem;
  gap: 6.4rem;
}

@media (max-width: 1024px) {
  .users-list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 384px) {
  .users-list {
    grid-template-columns: repeat(1, 1fr);
  }
}

.CTA-section {
  padding: 6.4rem 8rem;
}

.CTA-section--text {
  font-size: 1.8rem;
  font-weight: 500;
  color: #333;
}

.modal {
  z-index: 99;
  position: fixed;
  top: 16vh;
  left: 10%;
  width: 80%;
  background: #fff;
  box-shadow: 0 7px 30px -10px rgba(150, 170, 180, 0.9);
  border-radius: 11px;
  overflow: hidden;
}

.modal__header {
  width: 100%;
  text-align: center;
  padding: 1rem 0.5rem;
  color: #333;
}

.modal__header h2 {
  font-size: 2rem;
  margin: 0.5rem;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  padding: 1rem 0.5rem;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 32rem);
    width: 64rem;
  }
}

.modal-enter {
  -webkit-transform: translateY(-10rem);
          transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

.modal-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  -webkit-transform: translateY(-10rem);
          transform: translateY(-10rem);
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.error-modal-text {
  font-weight: 700;
  font-size: 2.4rem;
  color: #333;
}

